.body_wrap {
    display: grid;
    grid-template-columns: 250px 1fr; /*  */
    // grid-template-rows: 70px 1fr; /* header  footer main  */
    grid-template-areas: 
        // "header header"
        "aside main"
        "aside footer";
    min-height: 100vh; /*  */

    header {
        grid-area: header;
    }
    
    aside {
        grid-area: aside;
    }
    
    main {
        grid-area: main;
    }
    
    footer {
        grid-area: footer;
    }
}